import React, { PropTypes, Component } from 'react'
import foldCode from "./utils/codemirrorFoldCode"
import JSHighlight from "./utils/codemirrorJSHighlight"
import CodeMirror from 'codemirror'
import styles from './styles/CodeEditor.css'

var IS_MOBILE = (
  navigator.userAgent.match(/Android/i)
    || navigator.userAgent.match(/webOS/i)
    || navigator.userAgent.match(/iPhone/i)
    || navigator.userAgent.match(/iPad/i)
    || navigator.userAgent.match(/iPod/i)
    || navigator.userAgent.match(/BlackBerry/i)
    || navigator.userAgent.match(/Windows Phone/i)
)

var OPEN_MARK = /{{{/
var CLOSE_MARK = /}}}/

CodeMirror.registerGlobalHelper('fold', 'marked',
    function(mode, mirror) {
        return mode.name === 'javascript';
    },
    function(mirror, start) {
        var lineNo = start.line;
        var lineText = mirror.getLine(lineNo);
        var lineCount = mirror.lineCount();

        var openMatch = OPEN_MARK.exec(lineText);
        var closeMatch = CLOSE_MARK.exec(lineText);

        if (openMatch) {
            // search forwards
            for (var i = lineNo; i < lineCount; i++) {
                closeMatch = CLOSE_MARK.exec(mirror.getLine(i));
                if (closeMatch) {
                    return {
                        from: CodeMirror.Pos(lineNo, openMatch.index),
                        to: CodeMirror.Pos(i, closeMatch.index + 3)
                    };
                }
            }

        } else if (closeMatch) {
            // search backwards
            for (var i = lineNo; i >= 0; i--) {
                openMatch = OPEN_MARK.exec(mirror.getLine(i));
                if (openMatch) {
                    return {
                        from: CodeMirror.Pos(i, openMatch.index),
                        to: CodeMirror.Pos(lineNo, closeMatch.index + 3)
                    };
                }
            }
        }
    }
);

var ComponentCodeEditor = React.createClass({

  componentDidMount: function() {
      if (IS_MOBILE) return;
      this.originalCode = this.props.code;
      const node = this.refs.editor;
      this.editor = CodeMirror.fromTextArea(node, {
        mode: 'javascript',
        lineNumbers: false,
        lineWrapping: true,
        smartIndent: false,  // javascript mode does bad things with jsx indents
        matchBrackets: true,
        theme: 'solarized-light',
        scrollbarStyle: null,
        viewportMargin: Infinity,
        dragDrop: false,
        //theme: 'monokai',
        readOnly: this.props.readOnly
      });

      // foldCode on Load
      for (var i = 0; i < this.editor.doc.size; i++) {
          var line = this.editor.getLine(i);
          var match = OPEN_MARK.exec(line);
          var cursor = {line: i, ch: 3, xRel: 1};
          if (match &&
              // and the cursor is on it
              // (this is buggy if both occur on the same line)
              cursor.ch >= match.index &&
              cursor.ch < match.index + 3) {
              this.editor.foldCode(CodeMirror.Pos(i, 3), { widget: '...expand folded code ↴' });
          }
      }
      // attach event listeners
      this.editor.on('change', this.handleChange);
      this.editor.on('beforeSelectionChange', (instance, obj) => {

          // why is ranges plural?
          var selection = obj.ranges ?
              obj.ranges[0] :
              obj;

          var noRange = selection.anchor.ch === selection.head.ch &&
                        selection.anchor.line === selection.head.line;
          if (!noRange) {

              return;
          }

          var cursor = selection.anchor;
          var line = instance.getLine(cursor.line);
          var match = OPEN_MARK.exec(line) || CLOSE_MARK.exec(line);

          // the opening or closing mark appears on this line
          if (match &&
              // and the cursor is on it
              // (this is buggy if both occur on the same line)
              cursor.ch >= match.index &&
              cursor.ch < match.index + 3) {

                  instance.foldCode(cursor, { widget: '...expand folded code ↴' });
          }
      });
      // Do component highlighting
      this._highlightCurrentComponent(node);
      var that = this;
      setTimeout(function() {
         /* fix for longer code examples */
         that._updateHeight(node);
      }, 10);

  },
  _updateHeight(node){
    var parent = node.parentNode.parentNode.offsetHeight;

    var setHeight = node.parentNode.querySelectorAll('.CodeMirror-lines')[0].clientHeight;
    var wrapper = node.parentNode.querySelectorAll('.CodeMirror-scroll')[0];
    var sizer = node.parentNode.querySelectorAll('.CodeMirror-sizer')[0]

    var fixTall = (setHeight > 800) ? 120 : 20;
    wrapper.style.height = setHeight + fixTall;
    sizer.style.height = setHeight + fixTall;
  },
  _highlightCurrentComponent(node){
    var variables = node.nextSibling.querySelectorAll('.cm-variable');

    var previousSib;
    for (var i = variables.length - 1; i >= 0; i--) {
        var previousSib = variables[i].previousSibling;

        if(previousSib) {
            var isComponent = previousSib.innerText === "<";
            if(isComponent && variables[i].innerText === this.props.name) {
              var classN = variables[i].className;
              variables[i].className = classN + " current-component";
            }
        }

    }

    /* hightlight closing brackets */
    var ending = node.nextSibling.querySelectorAll('.cm-string-2, .cm-operator');
    for (var i = ending.length - 1; i >= 0; i--) {
       var isComponentClose = ending[i].innerText === "/>";

       if(isComponentClose) {
         var classN = ending[i].className;
         //ending[i].className = classN + " current-component";
       }
    }
  },
  shouldComponentUpdate: function(nextProps, nextState) {
    return nextProps !== this.props;
  },
  componentDidUpdate: function() {
    if (this.props.readOnly) {
      this.editor.setValue(this.props.code);
    }
  },

  handleChange: function() {
    if (!this.props.readOnly && this.props.onChange) {
      this.props.onChange(this.editor.getValue());
      const node = this.refs.editor;
      this._highlightCurrentComponent(node);
    }
  },
  _handleReset: function(){
    this.editor.setValue(this.originalCode);
    const node = this.refs.editor;
    this._highlightCurrentComponent(node);
  },
  render: function() {
    // wrap in a div to fully contain CodeMirror
    var editor;
    var code = this.props.code;
    var readOnly = (this.props.readOnly) ? "read-only" : "";

    if (IS_MOBILE) {
      editor = <pre style={{overflow: 'scroll'}}>{code}</pre>;
    } else {
      editor = <textarea ref="editor" defaultValue={code} />;
    }

    return (
      <div style={this.props.style} className={"editor-section " + readOnly}>
        <span className="editor-controls">
          <span onClick={this._handleReset} className="reset-code">
            reset
          </span>

        </span>
        {editor}
      </div>
    );
  }
});

module.exports = ComponentCodeEditor;


