import React, { PropTypes, Component } from 'react'
// import Row from '../../../../components/Row'
import styles from './styles/PropTypes.css'

export default class Properties extends Component {
  static propTypes = {
    attrs: PropTypes.object.isRequired,
  };
  renderPropTypes () {
    const { attrs } = this.props
    if (!attrs) {
      return false
    }
    return Object.keys(attrs).map(function (key) {
      if (key.match(/^_/)) {
        /* hide props that are private (start with underscore _) */
        return false
      }
      let props = attrs[key]
      let description
      let possibleValues
      const required = (props.required) ? props.required : false
      const type = (props.type) ? props.type.name : 'string'

      if (props.description) {
        // console.log(key + ' = ' + props.description)
        description = props.description
      }
      let options
      // console.log(props)

      if (props.type && props.type.name === 'enum' && typeof props.type.value === 'object') {
        options = props.type.value.map(function (val, i) {
          return (
            <div className='prop-value-wrapper' key={i}>
              <span className='prop-value'>{val.value.replace(/'/g, '')}</span>
            </div>
          )
        })
      }

      let defaultValueDisplay
      if (props.defaultValue && props.defaultValue.value) {
        defaultValueDisplay = (
          <div className='prop-defaultValues'>
            <span className='default-value-label'>Default:</span>
            <pre className='default-value'>
              {props.defaultValue.value.replace(/'/g, '')}
            </pre>
          </div>
        )
      }
      let optionsDisplay
      if (options && options.length) {
        optionsDisplay = (
          <div className='prop-possibleValues'>
            <span className='default-value-label'>Options:</span>
            {options}
          </div>
        )
      }

      const propType = (props.type && props.type.name) ? props.type.name : ''
      const possValues = (options && options.length) ? 'Possible values: ' : ''
      const req = (required) ? 'required' : ''
      return (
        <div className='component-properties' key={key}>
          <div align='left'>
              <span className='prop-title'>{key} - </span>
              <span className='prop-type'> {propType}</span>
              <span className='required'>{req}</span>
          </div>

          <div align='left' style={{marginBottom:'10px'}}>

            <div
              className='prop-description'
              dangerouslySetInnerHTML={{__html: description }}
            />
              {defaultValueDisplay}
              {optionsDisplay}
          </div>
        </div>
      )
    })
  }

  render () {
    const { attrs, name } = this.props
    const renderProps = this.renderPropTypes()
    return (
      <div className='properties'>
        <h2 className='propertiesTitle'>PropTypes</h2>
        <ul>
          {renderProps}
        </ul>
      </div>
    )
  }
}



