import React, { PropTypes, Component } from 'react'
import Fiddle from './Fiddle'
import ComponentSectionStyles from './styles/ComponentContainer.css'
// import List from '../../../../components/List'


export default class ComponentSection extends Component {
  static propTypes = {
    component: PropTypes.object.isRequired
  };

  render() {
    const { component } = this.props;
    return (
      <div id={component.name} className="component-section">
          { this._renderFiddles() }
      </div>
    );
  }
  componentDidMount() {
    document.body.scrollTop = 0
  }
  _renderFiddles() {
    const { component } = this.props;

    if(!component.examples) { return false }

    return component.examples.map(function(example, i) {
      let layout = "split";
      return(
        <div key={i} className={"component-code fiddle-layout-" + layout }>
        <Fiddle
            layout={layout}
            count={i}
            name={component.name}
            props={component.props}
            path={example.path}
            code={example.source} />
       </div>
      );
    });
  }
  _formatPath(path){
    // /anypoint-components/tree/master/
    return path.replace(/(.*)(?=src)/, "http://github.com/mulesoft/anypoint-components/tree/master/") + ".js";
  }

}



