import React, { PropTypes, Component } from 'react'
import LivePreview from "./LivePreview"
import ComponentContainer from './ComponentContainer'
import { Link } from 'react-router'

export default class SingleComponent extends Component {
  getComponentIndexFromURL (name) {
    if (window.examples[name]) {
      return window.examples[name]
    } else {
      return null
    }
  }
  _renderComponentFiddle (component) {
    if (component) {
      return <ComponentContainer key={component.name} component={component} />
    } else {
      return (
        <div>
          {this.props.params.name} Doesn't Exist!
        </div>
      )
    }
  }

  render () {
    var component = this.getComponentIndexFromURL(this.props.params.name)
    return (
      <div className='component-section'>
        <div
          style={{maxWidth: '50%', paddingLeft: 40, marginBottom: 20, marginTop: 10}}>
          {component.description}
        </div>
        {this._renderComponentFiddle(component)}
      </div>
    )
  }
}



