import React, { PropTypes, Component } from 'react'
import CodeEditor from './CodeEditor'
import LivePreview from './LivePreview'
import PropTypesDisplay from './PropTypes'
import formatCode from './utils/formatCode'

export default class Fiddle extends Component {

  static propTypes = {
    code: PropTypes.string.isRequired,
    layout: PropTypes.string
  }

  static defaultProps = {
    layout: 'split'
  }

  constructor (props) {
    super()
    let code = formatCode(props.code, props.name)
    this.state = {
      code: code
    }
    this.handleCodeChange = this.handleCodeChange.bind(this)
  }

  handleCodeChange (code) {
    this.setState({ code })
  }
  renderCodeEditor (name) {
    /*
    // todo make a textbox for IE
    return (
      <pre>{this.state.code}</pre>
    )
    /**/
    //*
    return (
      <CodeEditor
        name={name}
        onChange={this.handleCodeChange}
        code={this.state.code}
      />
    )
    /**/
  }
  render () {
    const { component, layout, name, count, path } = this.props
    const className = 'component-fiddle ' + 'layout-' + layout

    return (
      <div className={className}>
        <span className='preview-props-wrapper'>
          <LivePreview
            path={path}
            name={name}
            count={count}
            code={this.state.code}
          />

          {this._renderPropTypes()}

        </span>
        <span className='editor-wrapper'>
          {this.renderCodeEditor(name)}
        </span>
      </div>
    )
  }
  _renderPropTypes () {
    const { props, name, count } = this.props

    if (props && count === 0) {
      // Only show props one time
      return (
        <PropTypesDisplay name={name} attrs={props} />
      )
    }
  }
}



